import { Inline, InlineProperties, Data } from '@ali/4ever-cangjie';
import { ELEMENT_TYPE } from '../../utils/meta';

export interface StickerData extends Data {
  readonly code?: string;
  readonly width?: number;
  readonly height?: number;
}

export interface StickerProperties {
  data?: StickerData;
  nodes?: InlineProperties['nodes'];
}

export default class Sticker extends Inline {
  // 使用 emoji 而非 sticker ，是为兼容富文本输入框、评论等业务
  static TYPE = ELEMENT_TYPE;

  static create(properties: Sticker | StickerProperties | string) {
    if (typeof properties === 'string') {
      throw new Error('`Sticker.create` does not accept string');
    }
    if (Sticker.isSticker(properties)) {
      return properties;
    }
    return super.create({
      ...properties,
      type: Sticker.TYPE,
    }) as Sticker;
  }

  static isSticker(maybeSticker: any): maybeSticker is Sticker {
    return Inline.isInline(maybeSticker) && maybeSticker.type === Sticker.TYPE;
  }
}
